<template>
  <div class="hot">
    <header class="hotsong">
      <div class="yunmusic"></div>
      <div class="uptime">更新日期: {{ updata }}</div>
    </header>
    <ul class="hot-song-list">
      <HotSongItem
        v-for="(hot, index) in playlist"
        :key="index"
        :hot="hot"
        :index="index"
        @click.native="$emit('clickHandler', [hot, playlist])"
        :res="res"
        :autoplay="autoplay"
      />
      <!-- @res="$emit('hot.al.id')" -->
    </ul>
    <div class="box">
      <a>查看完整表单 ></a>
    </div>
  </div>
</template>

<script>
import HotSongItem from "@/components/HotSongItem.vue";
export default {
  components: {
    HotSongItem,
  },
  props: ["res", "autoplay"],
  data: function () {
    return {
      playlist: [],
      updata: [],
    };
  },

  created() {
    this.axios
      .get("https://apis.netstart.cn/music/playlist/detail?id=3778678")
      .then((res) => {
        // console.log(res, res.data.playlist.tracks, res.data.playlist.tracks[0].name);
        this.playlist = res.data.playlist.tracks.slice(0, 20);
        // console.log(res);
        // this.updatetime = new Date(res.data.playlist.updateTime)
        //   .toLocaleString()
        //   .slice(0, 9)
        //   .split("/");
        // console.log(res.data.playlist.tracks[0].sq);
        this.updata = new Date(res.data.playlist.updateTime)
          .toLocaleString()
          .slice(0, 9);
        this.$emit("updata-loading");
      });
  },
};
</script>

